<template>
  <div>
    <headerBar :header-data="headerData" />
    <div v-if="ryList && ryList.length>0" class="container">
      <div class="content">
        <div v-for="(item,index) of ryList" :key="index" class="workpreview-list" @click="qwsh(item)">
          <div class="workpreview-tit"> {{ item.rymc }}</div>
          <div class="workpreview-btn" onClick="event.cancelBubble = true" @click="shxq(item)">审核历史</div>
          <div class="workpreview-num"> 待审核：{{ item.count }}</div>
          <div class="workpreview-arrow"><i class="iconfont iconarrowright" /></div>
        </div>
      </div>
    </div>
    <div v-else>
      <error-page
        :page-style="pageStyles"
        @errorButton="parentReceive"
      />
    </div>
    <div class="common-space" />
    <!-- 底部按钮 begin -->
    <div class="foot-btn full fixed">
      <ul>
        <li><van-button type="info" round size="large" @click="shqb()">审核全部</van-button></li>
      </ul>
    </div>
    <!-- 底部按钮 end -->

  </div>
</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar.vue'
import { getUserName } from '@/common/js/catch'
import errorPage from '@/components/result-page/index'
import { pageRouter } from '@/common/js/mixin.js'

export default {
  name: 'ShGzrz',
  components: {
    headerBar,
    errorPage
  },
  mixins: [pageRouter],
  data() {
    return {
      headerData: {
        title: '审核情况预览',
        leftUrl: 'RzIndex',
        leftText: ' '
      },
      ryList: [],
      pageStyles: 0
    }
  },
  activated() {
    this.$store.commit('erp/SET_RY', {})
    this.$store.commit('erp/SET_CHECKINDEX1', '')
    this.$store.commit('erp/SET_CHECKINDEX', '')
    this.$store.commit('erp/SET_LIST', '')
    this.getShryList()
  },
  created() {
    this.$store.commit('erp/SET_RY', {})
    this.$store.commit('erp/SET_CHECKINDEX1', '')
    this.$store.commit('erp/SET_CHECKINDEX', '')
    this.$store.commit('erp/SET_LIST', '')
    this.getShryList()
  },
  methods: {
    shxq(item) {
      this.$router.push({ name: 'ShGzrzJL', params: { ry: item }})
    },
    parentReceive() {
      this.getShryList()
    },
    getShryList() {
      this.$store.dispatch('erp/getShryAction', {
        ryid: getUserName()
      }).then(response => {
        this.ryList = response
        if (this.ryList.length === 0) {
          this.pageStyles = 1
        }
      }).catch(error => {
        this.$toast(error)
      })
    },
    qwsh(item) {
      this.$store.commit('erp/SET_RY', item)
      this.$router.push('ShGzrz')
    },
    shqb() {
      let state = false
      for (let i = 0; i < this.ryList.length; i++) {
        if (this.ryList[i].count !== 0) {
          state = true
        }
      }
      if (state) {
        this.$router.push({ name: 'ShGzrz' })
      } else {
        this.$toast('当前无待审核日志！')
      }
    }
  }
}
</script>

<style scoped>
.van-collapse-item--border::after{display: none;}
.van-cell::after{display: none;}
/deep/.van-field__label{width: auto;}
.txyj{margin: 0 5px;}
</style>
